.App {
  min-height: 100vh;
  background-image: linear-gradient(-20deg, #f794a4 0%, #fdd6bd 100%);
  text-align: center;
}

.headline{
  margin: 0;
}
.searchForm{
min-height: 10vh;
display: flex;
justify-content: center;
align-items: center;
}

.searchBar{
  width: 50%;
  border: none;
  padding: 10px;
}

.searchBtn {
  background: lightcoral;
  padding: 10px 20px;
  color: white;
}

.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
