.recipe {
    width: 40%;
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(71, 71, 71);
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 40%;
    margin-bottom: 25px;
}

.name {
    font-style: italic;
}

.image {
    border-radius: 50%;
    width: 200px;
    height: 200px;
    margin-bottom: 20px;
}